.slider__wrapper {
  display: flex;
  flex-direction: column;
}

.slider__slide-info {
  display: none;
}

.slider__reviews {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.14;
  letter-spacing: normal;
  text-align: center;
  width: 283px;
  padding-left: 19px;
  margin-top: -3px;
  margin-bottom: 19px;
}

.slider__controls {
  position: relative;
  margin-top: 7px;
}

