.slider__slide-control::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  z-index: 80;
  width: 6px;
  height: 6px;
  background: black;
  opacity: 0.3;
  border-radius: 50%;
}

.slider__slide-control:active::after {
  opacity: 0.1;
}

.slider__slide-control:hover::after {
  opacity: 0.6;
}

.slider__slide-control::before {
  content: "";
  display: none;
  position: absolute;
  z-index: 100;
  width: 6px;
  height: 6px;
  margin: -5px;
  margin-right: -6px;
  border: 3px solid black;
  border-radius: 50%;
}

.slider__slide-control {
  position: relative;
  display: inline-block;
  width: 6px;
  height: 6px;
  padding: 11px;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  background: none;
  padding-left: 10px;

  &--active::before {
    display: block;
    margin-left: -3px;
  }

  &--active::after {
    display: none;
  }
}

