//  }
//}

.slider__slide-control__author-info {
  display: none;
}

.slider h3 {
  margin-top: 17px;
}

@media (min-width: @tablet-width) {
  .slider {
    width: 630px;
    text-align: center;
    position: relative;
    margin: 104px auto 0;
  }

  .slider__wrapper {
    width: 630px;
  }

  .slider__slide-wrapper {
    width: 500px;
    margin: 0 auto;
  }

  .slider__controls {
    display: block;
    margin-top: 5px;
  }

  .slider::before {
    display: none;
  }

  .slider__slide-info {
    margin-top: 10px;
    font-size: 14px;
    width: auto;
    display: block;
  }

  .slider__reviews {
    width: 620px;
    height: 79px;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: center;
    padding-left: 5px;
    padding-top: 5px;
  }

  .slider h3 {
    font-size: 20px;
    margin-bottom: 0;
    margin-top: 26px;
  }

  .slider__slide-control {
    &--active::before {
      margin-left: -5px;
    }
  }

  .slider__slide.active {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 9px;
    width: 630px;
    margin-top: 20px;
  }

  .slider__controls-tablet {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .slider {
    background: url("../img/quotes-svg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: calc(100% + -463px);
    background-position-x: 279px;
    padding-top: 183px;
    margin: 11px auto -5px;
  }

  .slider__slide-info {
    margin-top: 13px;
    font-size: 18px;
  }

  .slider__controls {
    display: none;
  }

  //.slider__slide-info {
  //  padding-top: 10px;
  //}

  .slider__reviews {
    margin-top: -33px;
    width: 688px;
    padding-left: 0;
    margin-left: -30px;
  }

  .slider h3 {
    font-size: 24px;
    margin-left: 4px;
    margin-top: 24px;
    margin-bottom: -9px;
  }

  .slider__slide.active {
    margin-bottom: 0;
  }

  .slider__slide-tablet {
    padding-bottom: 0;
  }
}
