.modal-form__button {
  width: 276px;
  height: 59px;
  background-color: #2aab6d;
  border-radius: 8px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: white;
  outline: none;
  border: 2px solid #2aab6d;
}

.modal-form {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 320px;
  background-color: white;
  text-align: center;
  box-shadow: 0, 14px, 28px, rgba(0, 0, 0, 0.25), 0, 10px, 10px, rgba(0, 0, 0, 0.22);

  &--good {
    transform: translate(-50%, 82%);
    display: none;
  }

  &--fail {
    transform: translate(-50%, 268%);
    display: none;
  }
}

//.modal-form {
//  &--fail {
//  transform: translate(-50%, -100%);
//  display: none;
//  }
//}

.modal-form__top {
  padding: 23px 20px 24px 20px;

  &--fail {
    padding-top: 19px;
    padding-bottom: 16px;
  }
}

.modal-form__title {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: center;
  color: black;
}

.modal-form__text {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: center;

  &--good {
    padding-left: 7px;
    padding-right: 7px;
  }
}

.modal-form__bottom {
  width: 276px;
  height: 59px;
  background-color: #2aab6d;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: white;
  outline: none;
  padding: 48px 20px 52px 20px;

  &--good {
    background-color: #ececec;
  }

  &--fail {
    padding-top: 20px;
    padding-bottom: 50px;
    background-color: white;
  }
}

.modal-form__button:hover {
  background-color: #1c9b5e;
}

.modal-form__button:active {
  color: rgba(255, 255, 255, 0.3);
}


@media (min-width: @tablet-width) {
  .modal-form__button {
    width: 296px;
  }

  .modal-form__bottom {
    padding-left: 80px;
    padding-right: 80px;
    width: 300px;

    &--fail {
      padding-top: 15px;
      padding-bottom: 52px;
      background-color: white;
    }
  }

  .modal-form {
    &--good {
      width: 460px;
      transform: translate(-50%, 82%);
    }

    &--fail {
      width: 460px;
      transform: translate(-50%, 129%);
    }
  }

  .modal-form__top {
    padding-left: 80px;
    padding-right: 80px;
  }

  .modal-form__title {
    margin-top: 18px;
    margin-bottom: 18px;
    font-size: 30px;
    line-height: 1.33;
  }

  .modal-form__text {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 1.33;
  }
}

@media (min-width: @desktop-width) {
  .modal-form__bottom {
    padding-left: 160px;
    padding-right: 160px;
    width: 459px;

    &--fail {
      padding-left: 80px;
      padding-right: 80px;
      padding-top: 60px;
      width: 619px;
      background-color: #ececec;
    }
  }

  .modal-form {
    width: 780px;

    &--good {
      transform: translate(-50%, 122%);
    }

    &--fail {
      transform: translate(-50%, 82%);
    }
  }

  //.modal-form__bottom {
  //  width: 780px;
  //}

  .modal-form__top {
    padding-left: 160px;
    padding-right: 160px;

    &--fail {
      padding-left: 80px;
      padding-right: 80px;
    }
  }
}
